import { createApi, fetchBaseQuery } from '@reduxjs/toolkit/query/react';
import { IGetAllTaxesResponse } from '../types/slicesTypes/taxApiTypes';
import { CTP_API_URL, CTP_PROJECT_KEY } from '../utils/variables';

export const taxApi = createApi({
  reducerPath: 'taxApi',
  baseQuery: fetchBaseQuery({
    baseUrl: `${CTP_API_URL}/${CTP_PROJECT_KEY}/tax-categories`,
  }),
  endpoints: (build) => ({
    getAllTaxes: build.query<IGetAllTaxesResponse, string>({
      query(token: string) {
        return {
          url: '',
          method: 'GET',
          headers: {
            Authorization: `Bearer ${token}`,
          },
        };
      },
    }),
  }),
});

export const { useGetAllTaxesQuery } = taxApi;
